@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,700;1,400;1,700&display=swap);

.todo {
    padding: 5px 10px 5px 15px;
    width: 90%;
    margin: 25px 0;
    border-bottom: 2px solid #2f3640;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.timerBtn, .deleteBtn {
    width: 40px;
    height: 40px;
    font-size: 11px;
    border-radius: 50%;
    margin: 5px;
    background-color: #2f3640;
    color: #fff;
}
.timer {
    height: 250px;
    width: 250px;
    border-radius: 50%;
    border: 1px solid #2f3640;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.timer button {
    padding: 8px;
    margin: 0px 5px;
    font-size: 15px;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    border: none;
    background-color: #2f3640;
    color: white;
}

.timer span {
    margin: 10px;
    font-size: 50px;
}
body {
  margin: 10vh 10vw;
  font-family: 'Karla', sans-serif;
  text-align: left;
  color: #2f3640;
  background-color: white;
}

.app {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.leftSide, .rightSide {
  width: 30em;
}

.list {
  overflow-y: scroll;
  max-height: 60vh;
  scroll-behavior: auto;
}

.rightSide {
  height: 70vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #2f3640;
}

.title {
  display: flex;
  align-items: flex-start;
  font-size: 42px;
}

.title span {
  text-indent: 2rem;
}

input {
  width: 200px;
  font-family: 'Karla', sans-serif;
  font-size: 18px;
  padding: 10px;
  margin: 0 5px 0 0;
  border: none;
  border-bottom: 2px #2f3640 solid;
}

input:focus, button:focus {
  outline: none;
}

.addBtn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  font-size: 20px;
  padding: 5px;
  background-color: #2f3640;
  color: white;
}

.taskNow {
  font-size: 50px;
  font-style: italic;
  margin-bottom: 20px;
}
